.App {
  text-align: left;
  font-family: 'Segoe UI';
}

.header {
  font-size: 25px;
  background-color: #000;
  padding-top: 1px;
  padding-bottom: 1px;
  margin-bottom: 15px;
}

.header h2 {
  color: #fff;
  text-align: center;
}

#zip_Info {
  padding-top: 10px;
  padding-bottom: 10px;
}

#zip_Info h3 {
  margin-left: 40px;
}

#info_Body {
  border: 1px solid black;
  margin-left: 30px;
  margin-right: 30px;
  border-radius: 30px;
  
  background-color: #fff;
}
#zipinfo_Body{
	border: 1px solid black;
	margin-left: 30px;
	margin-right: 30px;
	border-radius: 30px;
	background-color: #fff;
	width:7%
}

.info_Body, ul.no-bullets{
  list-style-type: none;
}

#searchBar {
  margin-bottom: 10px;
  display: inline-block;
}

#searchBar label {
  margin-left: 40px;
  font-weight: bold;
  color: #fff;
}

#display {
  margin-left: 10px;
  margin-right: 10px;
  margin-bottom: 15px;


  padding-top: 20px;
  padding-bottom: 20px;
  border: 1px solid #000;
  border-radius: 30px;
  background-color: #000;
}

#null {
  margin-left: 40px;
  color: #fff;
}

#submit_button{
	border: 1px solid #fff;
	border-radius: 5px;
	background-color: #000;
	color: #fff;
	padding:4px;
}

